.composition{
	height: 100%;
	display: grid;
	grid-template-rows: repeat(8,1fr);
	grid-template-columns: repeat(10,1fr);
	
	&__photo{
	width:100%;
	height: 100%;
	box-shadow:0 1.5rem 4rem rgba($color-black,.4);
	border-radius:2px;
	z-index: 10;
	outline-offset: 2rem;
	object-fit: cover;
	display: block;
	transition: all .3s;

	&--1{
		grid-row: 1/6;
		grid-column: 1/6;
	}

	&--2{
		grid-row: 2/7;
		grid-column: 5/10;

	}

	&--3{
		grid-row: 4/9;
		grid-column:3/8;
	}
	
	&:hover{
		outline:1.5rem solid $color-primary;
		transform: scale(1.05) translateY(-.5rem);
		box-shadow: 0 2.5rem 4rem rgba($color-black, .5);
		z-index: 20;
	 }
	}


&:hover &__photo:not(:hover){
		transform: scale(.95);
	}

}