.section-stories{
	position:relative;
}

.bg-video{
	position: absolute;
	height: 100%;
	width:100%;
	opacity: .15;
	z-index: -1;
	&__content{
		height: 100%;
		width: 100%;
		object-fit: cover;
	}
}

.story{
		width:75%;
		margin: 0 auto;
		box-shadow: 0 2rem 4rem rgba($color-black, .1);
		border-radius:3px;
		transform: skewX(-10deg);
		background-color: rgba($color-white,.6);
		padding: 5rem 3rem;
		margin-bottom: 5rem;
		color: #777;
			

	&__shape{
		width:15rem;
		height: 15rem;
		float: left;
		shape-outside: circle(50% at 50% 50%);
		clip-path: circle(50% at 50% 50%);
		transform: skewX(10deg);
		margin-right: 2rem;
	}	

	&__img{
		height: 100%;
		transform: translateX(-4rem) scale(1.2);
		transition:all .5s;
	}

	&__text{
		transform: skewX(10deg);
	}

	&__caption{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-35%,20%);
		color: $color-white;
		font-size: 2.5rem;
		font-weight: 700;
		opacity: 0;
		transition: all .5s;
	}

	&:hover &__caption{
		opacity: 1;
		transform: translate(-35%,-50%);
	}

	&:hover &__img{
		transform: translateX(-4rem) scale(1);
		filter:blur(3px) brightness(80%);
	}
}