@import './styles/abstracts/variables';

*,
*::after,
*::before{
	margin:0;
	padding: 0;
	box-sizing: border-box;
}

html{
	font-size: 62.5%; //1rem = 10px
}

body{
font-family: 'Dancing Script','Pacifico', cursive;
font-weight: 400;
font-size:1.6rem;
line-height: 1.7;
padding:3rem;
box-sizing: border-box;
}

::selection{
	background-color: $color-primary;
	color:$color-white;
}

