.header{
	height:95vh;
	background-image: linear-gradient(to right bottom,rgba(126,213,111,0.8),
		rgba(40,180,131,0.8)),url(../../img/nat-10.jpg);
	background-size:cover;
	clip-path: polygon(0 0, 100% 0,100% 75vh, 0 100%);
	position: relative;

	&__text-box{
		position:absolute;
		top:40%;
		left:50%;
		transform: translate(-50%,-50%);
		text-align: center;
	}

	&__logo-box{
		position: absolute;
		top:4rem;
		left:4rem;
	}

	&__logo{
		height: 6rem;
	}


}

.heading-primary{
	margin-bottom: 5rem;
		color:#fff;
	&--main{
		line-height: 7rem;
		display:block;
		font-size: 9rem;
		font-weight: 400;
		margin-bottom: 0;
		letter-spacing: 1rem;
		padding: 0;
		}

&--sub{
	display: block;
	font-size:3.5rem;
	margin-left: 8rem;
     }
}

