.section-feature{

display:grid;
grid-template-columns:repeat(4,1fr);
grid-gap:5rem;
padding:20rem 5rem;
background-image: linear-gradient(to right bottom,rgba($color-primary-light, 0.8),
	               rgba($color-primary-dark,0.8)),url(../../img/nat-4.jpg);
background-size: cover;
clip-path: polygon(0 20vh, 100% 0, 100% 93vh, 0 100%);
margin-top: -20vh;




}


.feature-box{
	padding: .5rem;
	background-color: rgba($color-white,.8);
	text-align: center;
	border-radius:.3rem;
	color:#777;
	box-shadow: 0 2rem 4rem rgba($color-black, .15);
	transition: all .3s;
	&__icon{
		font-size:6rem;
		background-image: linear-gradient(to right, $color-primary-light,$color-primary-dark);
		color: transparent;
		background-clip:text; 
		display: inline-block;
	}

	&:hover{
		transform: translateY(-1.1rem) scale(1.03);
	}
}