.footer{
	background-color: $color-grey-dark-3;
	color:$color-white;
	font-size: 2rem;
	font-weight: 700;
	padding:5rem 5rem;
	&__logo-box{
		text-align: center;
	}

	&__logo{
		height: 15rem;
		width: auto;
	}

	&__grid{
		display: grid;
		grid-template-columns: 1fr 1fr;
		margin-top: 2rem;
		grid-column-gap: 25rem;
	}
	&__list{
			border-top: 1px solid #777;

		  ul{
		  	margin-top: 2rem;
		display: flex;
		list-style: none;
		li{
			&:not(:last-child){
				margin-right: 3rem;
			}
		}
		}
	}

	&__copyright{
			border-top: 1px solid #777;
			p{
				margin-top: 2rem;
			}
	}
}