.navigation{

	&__checkbox{
	display: none;
	}

	&__button{
		background-color: $color-white;
		height: 7rem;
		width: 7rem;
		border-radius: 50%;
		position:fixed;
		top:6rem;
		right:6rem;
		z-index: 100;
		cursor: pointer;
		box-shadow: 0 1rem 3rem rgba($color-black,.1);

	}

	&__background{
		height:6rem;
		width: 6rem;
		border-radius:50%;
		position:fixed;
		top:6.5rem;
		right:6.5rem;
		background-image:radial-gradient($color-primary-light,$color-primary-dark);
		z-index:50;
		transition: transform .8s cubic-bezier(0.86,0,0.07,1);
	}

	&__checkbox:checked ~ &__background{
		transform: scale(80);
	}

	&__nav{
		height: 100vh;
		position: fixed;
		z-index:70;
		width: 100vw;
		visibility: hidden;
		width: 0%;
		opacity: 0;
		transition: all .8s cubic-bezier(0.68,-0.55,0.265,1.55);
	}

	&__list{
		display: inline-block;
		position: absolute;
		top:50%;
		left:50%;
		transform: translate(-50%,-50%);
		list-style: none;
		text-align: center;
		width: 20%;


	}

	&__link{
		
		font-weight: 700;
		font-size: 3rem;
		color: $color-white;
		background-image: linear-gradient(120deg,transparent 0%,transparent 50%, $color-white 50%);
		background-size: 220%;
		transition: all .4s;
		cursor: pointer;

		&:hover,
		&:active{
			background-position:100%;
			color:$color-primary;
			transform: translateX(1rem);
		}
	}

	&__checkbox:checked ~ &__nav{
		visibility: visible;
		width: 100%;
		opacity: 1;
	}

	&__icon{
		position: absolute;
		top:3.2rem;
		left:2.5rem;
		backface-visibility: hidden;

		&::before,
		&::after{
			width:3rem;
			height: 3px;
			border-radius: 3px;
			background-color: $color-grey-dark-3;
			display:inline-block;
			content: "";
			position: absolute;
			left: 0;
			transition: all .1s;
		}

		&::before{
			top:-.7rem;
			transform: rotate(40deg);
		}

		&::after{
			top:1rem;
			transform: rotate(-40deg);
		}

	}


&__checkbox:checked  + &__button &__icon{
	left:1.5rem;
	}

	&__checkbox:checked  + &__button &__icon::before{
			width: 4rem;
			top: 0;
			transform: rotate(135deg);	
	}

	&__checkbox:checked  + &__button &__icon::after{
			width: 4rem;
			top: 0;
			transform: rotate(-135deg);
	}
	
}