.section-book{
	padding: 15rem 0;
	background-image: linear-gradient(to right bottom, $color-primary-light,$color-primary-dark);
}

.book__form{
	background-image: linear-gradient(105deg, rgba($color-white,.9) 0%,rgba($color-white,.9) 50%,transparent 50%), url(../../img/nat-10.jpg);
	background-size: 100%;
	border-radius: .5rem;
	box-shadow: 0 1.5rem 4rem ($color-black, .5);
	margin: 0 5vw;
	color: #777;

	button{
		background-color:$color-primary;
		color: #fff;
	}

}

.form{
	width: 50%;
	padding: 6rem;

	&__group:not(last-child){
		margin-bottom: 1rem;
	}

	&__group--radio{
		display: flex;
		justify-content: space-between;
		width: 80%;
	}

	&__input{
		font-size: 1.6rem;
		font-family: inherit;
		border: none;
		border-radius: 3px;
		padding: 1.5rem 2rem;
		display: block;
		width:80%;
		transition: all .5s;

		&:focus{
			outline:none;
			box-shadow: 0 1rem 2rem rgba($color-black,.1);
			border-bottom:3px solid $color-primary;
		}
		
		&:focus:invalid{
			outline:none;
			border-bottom: 3px solid $color-secondary-dark;
		}

	}


	&__label{
		margin-left: 2rem;
		margin-top: .5rem;
		display: block;
		transition:all .3s;
	}

	&__input:placeholder-shown + &__label{
		opacity:0;
		visibility: hidden;
		transform: translateY(-4rem);
	}

	&__radio-group{
		margin-bottom: 1rem;
		display: inline-block;

	}

	&__radio-input{
		display: none;
	}

	&__radio-label{
		font-size: $default-font-size;
		cursor:pointer;
		position: relative;


	}

	&__radio-button{
		height: 3rem;
		width:3rem;
		border:5px solid $color-primary;
		border-radius:50%;
		display:inline-block;
		position: relative;
		right:.5rem;
		top:.8rem;

		&::after{
			content:"";
			height: 1.3rem;
			width:1.3rem;
			border-radius: 50%;
			position: absolute;
			top:50%;
			left: 50%;
			background-color: $color-primary;
			transform: translate(-50%,-50%);
			opacity: 0;
		}

	}

	&__radio-input:checked ~ &__radio-label &__radio-button::after{
		opacity: 1;
	}
}